
import footGuide from '../../components/footer/footGuide.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { getClassfiyList } from '../../service/getData'

export default {
components: {
Scroller,
footGuide,
Flexbox,
FlexboxItem
},
mounted () {
this.$vux.loading.show({
text: '...'
})
getClassfiyList().then(resolved =>{
try {
this.$vux.loading.hide()
this.menuItem = resolved.data
this.classVoList = this.menuItem[0].classVoList
} catch (error) {
this.$vux.toast.show({
text:'',
type:'text',
position:'top'
})
}

})
},
methods: {
openMenu (index) {
this.isactive = index
this.classVoList = this.menuItem[index].classVoList

},
openClassifyDetails(id,name){
this.$router.push({name:'ClassifyList',query:{pageIndex:1,id:id,name:name}})
},
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {
this.bottomCount += 10
this.$nextTick(() => {

})
this.onFetching = false
}, 2000)
}
}
},
data () {
return {
title: '',
menuItem: [],
isactive: 0,
classVoList:[]
}
}
}



